<template>
    <div class="hello-world">
        <div class="map-tools">
            <a @click="zoomIn">ÊîæÂ§ß</a>
            <a @click="zoomOut">Áº©Â∞è</a>
            <a @click="toHunan">ÊπñÂçó</a>
            <a @click="toChangsha">ÈïøÊ≤ô</a>
        </div>
        
        <!-- ‰∏≠ÂõΩÂõΩÊóóÂºπÂá∫Âä®Áîª -->
        <transition name="flag-pop">
            <img 
                v-if="showFlag" 
                src="/china-flag-waving.png" 
                class="china-flag-popup" 
                alt="‰∏≠ÂõΩÂõΩÊóó"
            />
        </transition>
        
        <div class="map-container" id="idmap" />
    </div>
</template>

<script setup>
    // ‰ªéVueÂØºÂÖ•ÁªÑÂêàÂºèAPIÂáΩÊï∞
    import { computed,onMounted,ref } from 'vue';
    
    // ÂØºÂÖ•OpenLayersÁöÑÊ†∏ÂøÉÊ®°Âùó
    import * as ol from 'ol';
    
    // ‰ªéOpenLayersÁöÑÊäïÂΩ±Ê®°ÂùóÂØºÂÖ•ÂùêÊ†áËΩ¨Êç¢ÂáΩÊï∞
    // fromLonLatÁî®‰∫éÂ∞ÜÁªèÁ∫¨Â∫¶ÂùêÊ†áËΩ¨Êç¢‰∏∫OpenLayers‰ΩøÁî®ÁöÑWebÂ¢®Âç°ÊâòÊäïÂΩ±ÂùêÊ†á
    import {fromLonLat} from 'ol/proj';
    
    // ÂØºÂÖ•Ëé∑ÂèñÈ´òÂæ∑Âú∞ÂõæÂõæÂ±ÇÁöÑÂáΩÊï∞
    // ‰ªé lib ÁõÆÂΩï‰∏ãÁöÑ amap_layers.js Êñá‰ª∂ÂØºÂÖ•
    import getAmapLayers from '@/lib/amap_layers.js';
    
    // ÂØºÂÖ•OpenLayersÁöÑÊéß‰ª∂Ê®°Âùó,Âπ∂ÂëΩÂêç‰∏∫ctrls
    // Êéß‰ª∂ÊòØÂú∞Âõæ‰∏äÁöÑ‰∫§‰∫íÂ∑•ÂÖ∑,Â¶ÇÁº©ÊîæÊåâÈíÆ„ÄÅÊØî‰æãÂ∞∫Á≠â
    import * as ctrls from 'ol/control';

    // ÂàõÂª∫ÂìçÂ∫îÂºèÂºïÁî®Êù•Â≠òÂÇ®Âú∞ÂõæÂÆû‰æã
    // ref ÂèòÈáèÔºåÂìçÂ∫îÂºèÔºåÂèØ‰ª•Âú®Ê®°ÊùøÂíåÂáΩÊï∞‰∏≠ËÆøÈóÆ
    const olmap = ref(null);
    
    // ÊéßÂà∂ÂõΩÊóóÊòæÁ§∫ÁöÑÂìçÂ∫îÂºèÂèòÈáè
    const showFlag = ref(false);
    
    // onMountedÂú®ÁªÑ‰ª∂ÁöÑDOMÂÖÉÁ¥†Ê∏≤ÊüìÂÆåÊàêÂêéÊâßË°å
    // ÂøÖÈ°ªÁ≠âDOMÂáÜÂ§áÂ•ΩÂêéÊâçËÉΩÂàõÂª∫Âú∞Âõæ,Âê¶ÂàôÊâæ‰∏çÂà∞id‰∏∫'idmap'ÁöÑÂÖÉÁ¥†
    onMounted(()=>{
        // ÂàõÂª∫Âú∞ÂõæÊéß‰ª∂ÈõÜÂêà
        // ctrls.defaults({})Ëé∑ÂèñÈªòËÆ§Êéß‰ª∂(Áº©Êîæ„ÄÅÊóãËΩ¨„ÄÅÂΩíÂ±ûÁ≠â)
        // .extend()Ê∑ªÂä†È¢ùÂ§ñÁöÑËá™ÂÆö‰πâÊéß‰ª∂
        const controls = ctrls.defaults({}).extend([
            new ctrls.ZoomToExtent({
              extent: [
                  ...fromLonLat([80, 2]), // Ë•øÂçóËßí
                  ...fromLonLat([135, 55]) // ‰∏úÂåóËßí
              ],
              label: 'üá®üá≥',
              tipLabel: 'Áº©ÊîæËá≥‰∏≠ÂõΩ',
              className: 'zoom-to-china',  // Ëá™ÂÆö‰πâCSSÁ±ªÂêç
            }),      // Áº©ÊîæÂà∞ÊåáÂÆöËåÉÂõ¥ÁöÑÊåâÈíÆ
            new ctrls.ScaleLine(),         // ÊØî‰æãÂ∞∫
            new ctrls.FullScreen(),        // ÂÖ®Â±èÊåâÈíÆ
            new ctrls.MousePosition({
                projection: 'EPSG:4326',  // ‰ΩøÁî®ÁªèÁ∫¨Â∫¶ÂùêÊ†áÁ≥ª
                className: 'mouse-position',  // Ëá™ÂÆö‰πâCSSÁ±ªÂêç
                // Ëá™ÂÆö‰πâÂùêÊ†áÊ†ºÂºèÔºö‰øùÁïô4‰ΩçÂ∞èÊï∞
                coordinateFormat: function(coordinate) {
                    if (!coordinate) return '';
                    const lon = coordinate[0].toFixed(4);  // ÁªèÂ∫¶Ôºå‰øùÁïô4‰ΩçÂ∞èÊï∞
                    const lat = coordinate[1].toFixed(4);  // Á∫¨Â∫¶Ôºå‰øùÁïô4‰ΩçÂ∞èÊï∞
                    return `ÁªèÂ∫¶: ${lon}¬∞ | Á∫¨Â∫¶: ${lat}¬∞`;
                }
            })
        ]);

        // ÂàõÂª∫OpenLayersÂú∞ÂõæÂÆû‰æã,Âπ∂Â≠òÂÇ®Âà∞ olmap.value ‰∏≠
        olmap.value = new ol.Map({
            // target: ÊåáÂÆöÂú∞ÂõæÊ∏≤ÊüìÁöÑÁõÆÊ†áDOMÂÖÉÁ¥†ÁöÑid
            target: 'idmap',
            // loadTilesWhileInteracting: Âú®Áî®Êà∑ÊãñÂä®ÊàñÁº©ÊîæÂú∞ÂõæÊó∂ÊòØÂê¶ÁªßÁª≠Âä†ËΩΩÁì¶ÁâáÔºå‰∫§‰∫íÊó∂‰πüÂä†ËΩΩ,‰ΩøÂú∞ÂõæÊõ¥ÊµÅÁïÖ
            loadTilesWhileInteracting: true,
            // layers: Âú∞ÂõæÂõæÂ±ÇÊï∞ÁªÑ
            // Ë∞ÉÁî®getAmapLayers()Ëé∑ÂèñÈ´òÂæ∑Âú∞ÂõæÁöÑÂ∫ïÂõæÂõæÂ±Ç
            layers: getAmapLayers(),
            // view: Âú∞ÂõæËßÜÂõæ,ÊéßÂà∂Âú∞ÂõæÁöÑ‰∏≠ÂøÉÁÇπ„ÄÅÁº©ÊîæÁ∫ßÂà´Á≠â
            view: new ol.View({ 
                // center: Âú∞ÂõæÂàùÂßã‰∏≠ÂøÉÁÇπ
                center: fromLonLat([112, 28.5]), 
                // zoom: ÂàùÂßãÁº©ÊîæÁ∫ßÂà´,Êï∞Â≠óË∂äÂ§ßË∂äËØ¶ÁªÜ
                zoom: 5 
            }),
            // controls: Â∫îÁî®‰∏äÈù¢ÂàõÂª∫ÁöÑÊéß‰ª∂ÈõÜÂêà
            controls,
        });
        
        // ÁõëÂê¨"Áº©ÊîæËá≥‰∏≠ÂõΩ"ÊåâÈíÆÁöÑÁÇπÂáª‰∫ã‰ª∂
        // ‰ΩøÁî® setTimeout Á°Æ‰øù DOM Â∑≤ÁªèÊ∏≤Êüì
        setTimeout(() => {
            const zoomToChinaBtn = document.querySelector('.zoom-to-china button');
            if (zoomToChinaBtn) {
                zoomToChinaBtn.addEventListener('click', () => {
                    // ÊòæÁ§∫ÂõΩÊóó
                    showFlag.value = true;
                    
                    // 1.5ÁßíÂêéÈöêËóèÂõΩÊóó
                    setTimeout(() => {
                        showFlag.value = false;
                    }, 800);
                });
            }
        }, 100);
    });

    // ‰∫§‰∫íÂáΩÊï∞
    // Ëøô‰∫õÂáΩÊï∞ÂÆö‰πâÂú® setup È°∂Â±Ç,ÂèØ‰ª•Âú®Ê®°Êùø‰∏≠Áõ¥Êé•‰ΩøÁî®

    function zoomIn(){ 
        if(olmap.value) {
            const view = olmap.value.getView();
            view.setZoom(Math.min(view.getZoom() + 1, 19));
        }
    }

    function zoomOut(){ 
        if(olmap.value) {
            const view = olmap.value.getView();
            view.setZoom(Math.max(view.getZoom() - 1, 1));
        }
    }

    function toHunan(){
        if(olmap.value) {
            const view = olmap.value.getView();
            view.setCenter(fromLonLat([112, 27]));
            view.setZoom(7);
        }
    }

    function toChangsha(){
        if(olmap.value) {
            const view = olmap.value.getView();
            view.setCenter(fromLonLat([112.9598, 28.21]));
            view.setZoom(10);
        }
    }
</script>

<style scoped>
