    padding: 0;
    margin: 0;
    width: 100vw;
    height: 100vh;
    position: relative;
    overflow: hidden; /* ÈöêËóèÊ∫¢Âá∫ÂÜÖÂÆπ */
  }
  
  .map-container {
    width: 100%;
    height: 100%;
  }

  /* ‰∏≠ÂõΩÂõΩÊóóÂºπÂá∫Âä®Áîª */
  .china-flag-popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9999;  /* Á°Æ‰øùÂú®ÊúÄ‰∏äÂ±Ç */
    width: 400px;
    height: auto;
    pointer-events: none;  /* ‰∏çÈòªÊå°Èº†Ê†á‰∫ã‰ª∂ */
  }

  /* ËøõÂÖ•Âä®ÁîªÔºö‰ªé0Áû¨Èó¥ÊîæÂ§ßÂà∞200px */
  .flag-pop-enter-active {
    transition: all 0.15s ease-out;
