  /* Á¶ªÂºÄÂä®ÁîªÔºöËæÉÂø´Áº©Â∞èÊ∂àÂ§± */
  .flag-pop-leave-active {
    transition: all 0.15s ease-in;
  }

  /* ËøõÂÖ•ÂâçÁöÑÁä∂ÊÄÅÔºöÁº©Êîæ‰∏∫0 */
  .flag-pop-enter-from {
    transform: translate(-50%, -50%) scale(0);
    opacity: 0;
  }

  /* ËøõÂÖ•ÂêéÁöÑÁä∂ÊÄÅÔºöÊ≠£Â∏∏Â§ßÂ∞è */
  .flag-pop-enter-to {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }

  /* Á¶ªÂºÄÂâçÁöÑÁä∂ÊÄÅÔºöÊ≠£Â∏∏Â§ßÂ∞è */
  .flag-pop-leave-from {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }

  /* Á¶ªÂºÄÂêéÁöÑÁä∂ÊÄÅÔºöÁº©Â∞èÂà∞0 */
  .flag-pop-leave-to {
    transform: translate(-50%, -50%) scale(0);
    opacity: 0;
  }

  /* Èº†Ê†á‰ΩçÁΩÆÂùêÊ†á */
  :deep(.mouse-position) {
    position: fixed;
    top: auto;
    right: 10px;
    left: auto;
    bottom: 10px;

    background-color: rgba(80, 80, 80, 0.85);
    color: white;
    padding: 8px 15px;
    border-radius: 8px;  /* ÂúÜËßí */
    font-size: 13px;
    font-family: 'Consolas', 'Monaco', monospace;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
    z-index: 998;
    backdrop-filter: blur(5px);  /* ËÉåÊôØÊ®°Á≥ä */
    border: 1px solid rgba(255, 255, 255, 0.15);  /* Ê∑°ËæπÊ°Ü */
  }

  /* Áº©ÊîæÊåâÈíÆÁªÑ - Âè≥‰∏ãËßí */
  :deep(.ol-zoom) {
    position: fixed;
    top: auto;
    right: 10px;
    left: auto;
    bottom: 80px;  /* Âú®Èº†Ê†á‰ΩçÁΩÆ‰∏äÊñπ */
    
    background-color: rgba(80, 80, 80, 0.85);
    backdrop-filter: blur(5px);
    border-radius: 8px;
