  }
  
  .map-tools {
    position: fixed;
    top: 10px;
    left: 50%;
    transform: translateX(-50%); /* Â±Ö‰∏≠ÂÅèÁßª */
    z-index: 998; /* Á°Æ‰øùÂú®Âú∞Âõæ‰∏äÊñπ */
    background-color: rgba(80, 80, 80, 0.85);
    backdrop-filter: blur(5px); /* ËÉåÊôØÊ®°Á≥ä */
    border-radius: 25px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
    padding: 8px 20px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    display: flex;
    gap: 5px; /* ÊåâÈíÆ‰πãÈó¥ÁöÑÈó¥Ë∑ù */
  }
  
  /* ÊåâÈíÆÊ†∑Âºè */
  .map-tools a {
    padding: 8px 16px;
    color: #ffffff;
    cursor: pointer;
    text-decoration: none; /* ÂéªÈô§‰∏ãÂàíÁ∫ø */
    font-size: 14px;
    font-weight: 500;

    background: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    border: 1px solid rgba(255, 255, 255, 0.2);

    transition: all 0.3s ease;
    user-select: none; /* Èò≤Ê≠¢ÊñáÂ≠óË¢´ÈÄâ‰∏≠ */
  }
  
  /* Èº†Ê†áÊÇ¨ÂÅúÊïàÊûú */
  .map-tools a:hover {
    background: rgba(255, 255, 255, 0.8);
    color: #1e1e1e;
    transform: translateY(-1px); /* ËΩªÂæÆ‰∏äÊµÆ */
    box-shadow: 0 4px 10px rgba(102, 126, 234, 0.4);
    border-color: rgba(255, 255, 255, 0.4);
  }
  
  /* ÁÇπÂáªÊïàÊûú */
  .map-tools a:active {
    transform: translateY(0);
    box-shadow: 0 2px 5px rgba(102, 126, 234, 0.3);
  }
  
  /* Â∞èÂ±èÂπïÊó∂Ë∞ÉÊï¥ */
  @media (max-width: 768px) {
    .map-tools {
      left: 70px;
